import { createClient } from './auth';

const supabase = createClient();

export async function testSupabaseConnection() {
  try {
    console.log('Testing Supabase connection...');
    
    // Test 1: Check if we can get the current user
    const { data: { user }, error: userError } = await supabase.auth.getUser();
    console.log('User check:', { user: user?.id, error: userError });
    
    if (!user) {
      throw new Error('No authenticated user found');
    }
    
    // Test 2: Try to query the trips table
    const { data: trips, error: tripsError } = await (supabase as any)
      .from('trips')
      .select('*')
      .limit(1);
    
    console.log('Trips query:', { trips, error: tripsError });
    
    if (tripsError) {
      throw new Error(`Database query failed: ${tripsError.message}`);
    }
    
    console.log('Supabase connection test passed!');
    return true;
  } catch (error) {
    console.error('Supabase connection test failed:', error);
    throw error;
  }
}
